import Vue from 'vue'
import Router from 'vue-router'
import Frame from '@/views/Frame'
const Login = () => import(/* webpackChunkName: "login" */'@/views/Login')
const Register = () => import(/* webpackChunkName: "login" */'@/views/Register')
const Homepage = () => import(/* webpackChunkName: "homepage" */'@/views/homepage')
const Products = () => import(/* webpackChunkName: "products" */'@/views/products')
const Blog = () => import(/* webpackChunkName: "blog" */'@/views/blog')
const Prices = () => import(/* webpackChunkName: "prices" */'@/views/prices')
const CasesFrame = () => import(/* webpackChunkName: "cases" */'@/views/cases/CasesFrame')
const Cases = () => import(/* webpackChunkName: "cases" */'@/views/cases')
const CaseDetail = () => import(/* webpackChunkName: "cases" */'@/views/cases/CaseDetail')
const ConsoleFrame = () => import(/* webpackChunkName: "console" */'@/views/console/ConsoleFrame')
const ConsoleIndex = () => import(/* webpackChunkName: "console" */'@/views/console/index')
const ConsoleApps = () => import(/* webpackChunkName: "console" */'@/views/console/myapps')
const ConsoleData = () => import(/* webpackChunkName: "console" */'@/views/console/mydata')
const ConsoleMain = () => import(/* webpackChunkName: "console" */'@/views/console/main')
const ShareMap = () => import(/* webpackChunkName: "cases" */'@/views/cases/ShareMap')

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'frame',
      component: Frame,
      redirect: '/homepage',
      children: [
        {
          path: 'homepage',
          component: Homepage
        },
        {
          path: 'products',
          component: Products
        },
        {
          path: 'blog',
          component: Blog
        },
        {
          path: 'prices',
          component: Prices
        },
        {
          path: 'cases',
          component: CasesFrame,
          children: [
            {
              path: '',
              component: Cases
            },
            {
              path: ':id',
              component: CaseDetail,
            }
          ]
        }
      ]
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/register',
      name: 'register',
      component: Register
    },
    {
      path: '/console',
      name: 'console',
      component: ConsoleFrame,
      redirect: '/console/index',
      children: [
        {
          path: 'index',
          name: 'console_index',
          component: ConsoleIndex
        },
        {
          path: 'myapp',
          name: 'console_myapp',
          component: ConsoleApps
        },
        {
          path: 'mydata',
          name: 'console_mydata',
          component: ConsoleData
        }
      ]
    },
    {
      path: '/console/main',
      name: 'console_main',
      component: ConsoleMain
    },
    {
      path: '/apps/:id/shares',
      component: ShareMap
    }
  ]
})



