// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueResource from 'vue-resource'
import App from './App'
import router from './router'
import 'iview/dist/styles/iview.css'
import '@/styles/reset.scss'
// import iView from 'iview'

// Vue.use(iView)
Vue.use(VueResource)
Vue.config.productionTip = false

/* 全局守卫-权限控制 */
router.beforeEach((to, from, next) => {
  console.log('-path-' + to.path)
  if (to.path.indexOf('console') > 0) {
    Vue.http.get('/pub/checkUser/isOrNoLogin', '').then(
      function (response) {
        if (response.data.flag) {
          next()
        } else {
          next({ path: '/login' })
        }
      },
      function (response) {
        next({ path: '/login' })
      }
    )
  }
  next()
})

//全局分享组件
Vue.component('my-share', {
  // 选项
  props: ['url'],
  template: '<div class="share-component" data-sites="qzone,qq,weibo,douban" :data-url="url" ></div>',
  // 实体DOM初始化并未结束 选项
  // mounted: function () {
  //   socialShare('.social-share, .share-component');
  // },
  updated: function () {
    socialShare('.social-share, .share-component');
  }
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



