<template>
  <!-- 模版页面（固定导航条＋可变内容） -->
  <div class="content">
    <Navbar :navList="navList">
      <ul slot="other" class="ivu-menu ivu-menu-dark ivu-menu-horizontal">
        <li class="ivu-menu-item"><a href="/login">登录</a></li>
        <li class="ivu-menu-item"><a href="/register">注册</a></li>
      </ul>
    </Navbar>
    <div class="clearfix"></div>
    <router-view></router-view>
    <!-- BEGIN: footer -->
    <iFooter></iFooter>
    <!-- END: footer -->
  </div>
</template>

<script>
export default {
  name: 'frame',
  components: {
    Navbar: () => import(/* webpackChunkName:common */'@/components/navbar'),
    iFooter: () => import(/* webpackChunkName: common */ '@/components/footer')
  },
  data () {
    return {
      navList: [
        {text: '首页', path: '/homepage'},
        {text: '产品', path: '/products'},
        {text: '分享', path: '/cases'},
        {text: '帮助中心', path: '/docs'},
        {text: '定价', path: '/prices'},
        {text: 'Blog', path: '/blog'}
      ]
    }
  }
}
</script>



